import { Component, OnInit } from '@angular/core';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-unit-user-report',
  templateUrl: './unit-user-report.component.html',
  styleUrls: ['./unit-user-report.component.scss']
})

export class UnitUserReportComponent implements OnInit {
  
  reportDataList:any;

  isDataLoaded:boolean = false;

  constructor(
    private lookupService: LookupService,
    private sessionService:SessionService
  ) { }

  formatName(name){
    return name.replace(/^\s+|\s+$/g,"");
  }

  ngOnInit() {

    let details = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 87, //lookuptypeid for report
      MenuName: 'UnitUser'
    }

    this.lookupService.getLookupValuesByApartmentIdLookupTypeIdMenuName(details).subscribe((res:any) => {

      this.reportDataList = res;

      this.isDataLoaded = true;

    })


  }

  getReportRedirectPath(path){
    return path.replace('/','');
  }

}
